/*
App.css
*/

/*fonts by google... well delivered by google*/

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

.icon {
  font-family: "Material Icons", serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
          font-feature-settings: "liga";
}

/* variables */

:root {
  --main-color: #1d1d1d;
  --text-color: #101010;
  --text-color-light: #101010;
  --border-color: #000;
  --bg-color: #fff;
  --neutral-color: #fff;
}

/* general */

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: "Open Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.5;
  color: var(--text-color);
  background: var(--main-color);
}

header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid var(--border-color);
  background: -webkit-gradient(linear,left top,right top,from(#c9373f),to(#930f5b));
}

header #logo {
  font-size: 175%;
  text-align: center;
  color: var(--main-color);
  line-height: 1;
}

header #logo .icon {
  padding-right: 0.25em;
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 5em;
  max-width: 50em;
}

/* grid */

.row {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  -ms-flex-align: center;
      align-items: center;
}

.col {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  max-width: 100%;
}

.col-start {
  -ms-flex-pack: start;
      justify-content: flex-start;
  text-align: left;
}

.col-center {
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.col-end {
  -ms-flex-pack: end;
      justify-content: flex-end;
  text-align: right;
}

/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 100%;
  background: -webkit-gradient(linear,left top,right top,from(#c9373f),to(#930f5b));
  border: 1px solid var(--border-color);
}

.calendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid var(--border-color);
}

.calendar .header .icon {
  cursor: pointer;
  -webkit-transition: 0.15s ease-out;
  -o-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
}

.calendar .header .icon:hover {
  -webkit-transform: scale(1.75);
      -ms-transform: scale(1.75);
          transform: scale(1.75);
  -webkit-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
  color: var(--main-color);
}

.calendar .header .icon:first-of-type {
  margin-left: 1em;
}

.calendar .header .icon:last-of-type {
  margin-right: 1em;
}

.calendar .days {
  text-transform: uppercase;
  font-weight: 400;
  color: var(--text-color-light);
  font-size: 70%;
  padding: 0.75em 0;
  border-bottom: 1px solid var(--border-color);
}

.the-body{
  background: -webkit-gradient(linear,left top,right top,from(#c9373f),to(#930f5b));
}
.calendar .the-body .cell {
  position: relative;
  height: 5em;
  border-right: 1px solid var(--border-color);
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
}

.calendar .the-body .cell:hover {
  background: var(--bg-color);
  -webkit-transition: 0.5s ease-out;
  -o-transition: 0.5s ease-out;
  transition: 0.5s ease-out;
}

.calendar .the-body .selected {
  border-left: 10px solid transparent;
  -webkit-border-image: -webkit-linear-gradient(45deg, #1d1d1d 0%, #101010 40%);
       -o-border-image: -o-linear-gradient(45deg, #1d1d1d 0%, #101010 40%);
          border-image: linear-gradient(45deg, #1d1d1d 0%, #101010 40%);
  border-image-slice: 1;
}

.calendar .the-body .row {
  border-bottom: 1px solid var(--border-color);
}

.calendar .the-body .row:last-child {
  border-bottom: none;
}

.calendar .the-body .cell:last-child {
  border-right: none;
}

.calendar .the-body .cell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: 0.75em;
  right: 0.75em;
  font-weight: 700;
}

.calendar .the-body .disabled {
  color: var(--text-color-light);
  pointer-events: none;
}

.calendar .the-body .cell .bg {
  font-weight: 700;
  line-height: 1;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -0.2em;
  right: -0.05em;
  -webkit-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
  letter-spacing: -0.07em;
}

.calendar .the-body .cell:hover .bg,
.calendar .the-body .selected .bg {
  opacity: 0.05;
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}

.calendar .the-body .col {
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-preferred-size: calc(100% / 7);
      flex-basis: calc(100% / 7);
  width: calc(100% / 7);
}



